import { CButton, CDataTable, CInput, CRow, CCol, CModal, CModalHeader, CModalBody } from '@coreui/react';
import {React, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import Select from 'react-select'
import { uid } from 'uid';
import supabase from '../../../supabase'

const Viavalen = () =>{
    const history = useHistory()
    const [modal, setModal] = useState(false)
    const dispatch = useDispatch()
    const formatGroupLabel = data => (
        <div style={groupStyles}>
          <span>{data.label}</span>
          <span style={groupBadgeStyles}>{data.options.length}</span>
        </div>
    );
    const groupStyles = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
      };
      const groupBadgeStyles = {
        backgroundColor: '#EBECF0',
        borderRadius: '2em',
        color: '#172B4D',
        display: 'inline-block',
        fontSize: 12,
        fontWeight: 'normal',
        lineHeight: '1',
        minWidth: 1,
        padding: '0.16666666666667em 0.5em',
        textAlign: 'center',
    };    
    const createNewAju = async () => {
        const { data, error } = await supabase
        .from('td_superset')
        .insert([
          { 'no_aju': uid()},
        ])
        if(!error){
            dispatch({type:'set',datasupabase:data[0]})
            history.push('ver1/form')            
        }        
    }   
    const handleInputChange = (e) =>{
        if(e.value == 'X' || e.value == 'A'){
            dispatch({type:'set',nella:e.value})            
        }
    } 
    return(
        <>
        <CModal
        style={{borderRadius:'20px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}
        show={modal}
        size="lg"
        onClose={()=>setModal(false)}
        >
            <CModalBody className="px-5 py-5">
                <div className="d-flex flex-column align-items-center">
                    <div><h5>Pilih Jenis Pengajuan</h5></div>
                    <div className="mb-4"><img src="https://pabean.insw.go.id/static/media/Info.81360c8f.svg" alt="Info Kepabeanan"/></div>
                    <div className="mb-2" style={{width:'100%'}}><Select
                    formatGroupLabel={formatGroupLabel}   
                    onChange={(e)=>handleInputChange(e)}                
                    options={
                        [
                            {
                              label: "SSm Ekspor",
                              options: [
                                {label:'Pabean dan/atau Karantina', value:'X'},
                                {label:'Pabean dan/atau Karantina dan SKA', value:'A'},                                  
                              ]
                            },                                                      
                            {
                                label: "Kawasan Ekonomi Khusus",
                                options: [
                                  { label: "Pengeluaran TLDDP", value: "7" },
                                  { label: "Free Movement", value: "8" },
                                  { label: "Pengeluaran Fasilitas", value: "9" },
                                  { label: "Pemasukan TLDDP", value: "10" },
                                  { label: "Pemasukan LDP", value: "11" },
                                  { label: "Pengeluaran LDP", value: "12" }                                                                                               
                                ]
                              }                            
                        ]                        
                    }
                    /></div>
                    <div style={{width:'100%'}}>
                        <CButton onClick={()=>createNewAju()} style={{width:'100%', backgroundColor:'#02275d', color:'white'}} className="btn">OK</CButton>
                    </div>                    
                </div>
            </CModalBody>
        </CModal>
        <div className="card px-4 py-4" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex justify-content-center mb-4">             
                <div><h5>Beranda Permohonan</h5></div>
            </div>
            <CRow className="mb-4">
                <CCol xs="11">
                    <CRow>
                        <CCol md="3">
                            <CInput placeholder="Nomor Permohonan"/>
                        </CCol>
                        <CCol md="3">
                            <CInput placeholder="Nomor Pendaftaran"/>
                        </CCol>
                        <CCol md="3">
                            <CInput placeholder="NPWP"/>
                        </CCol>  
                        <CCol md="3">
                            <CInput placeholder="Nama Perusahaan"/>
                        </CCol>                                                                        
                    </CRow>
                </CCol>
                <CCol xs="1" >
                    <CButton onClick={()=>setModal(true)} style={{backgroundColor:'#02275d', color:'white', borderRadius:'8px'}} className="btn ml-3"><i class="fas fa-plus"></i></CButton>
                </CCol>
            </CRow>
            <CDataTable
            addTableClasses="joss"
            fields={[
                {key:'no', label:'Nomor'},
                {key:'nopermohonan', label:'Nomor Permohonan'},
                {key:'tglpermohonan', label:'Tanggal Permohonan'},
                {key:'nopendaftaran', label:'Nomor Pendaftaran'},
                {key:'tglpendaftaran', label:'Tanggal Pendaftaran'},
                {key:'npwp', label:'NPWP'},
                {key:'nmperusahaan', label:'Nama Perusahaan'},
                {key:'action', label:'Action'},
            ]}
            />  
        </div>
        </>
    )
}

export default Viavalen